<template>
    <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'BarChart',
  data () {
    return {
      myChart: {},
      datas: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.datas) {
        if (item.name === null) {
          item.name = ''
        }
        Arr.push(item.name)
      }
      return Arr
    },
    rwcs: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.rwcs)
      }
      return Arr
    },
    gjlc: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push((item.gjlc))
      }
      return Arr
    },
    rwsc: function () {
      let Arr = []
      for (let item of this.datas) {
        Arr.push(item.rwsc)
      }
      return Arr
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 获取字段列表 */
    dataHandle () {
      this.datas = this.data
      this.drawLine()
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        grid: {
          left: '30px',
          right: '30px',
          top: '20%',
          containLabel: true
        },
        legend: {
          data: ['巡查次数', '巡查里程', '巡查时长'],
          top: 10,
          right: 60
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          },
          formatter: function (params) {
            return params[0].name + '<br>' + params[0].seriesName + ':' + params[0].value + '次' + '<br>' + params[1].seriesName + ':' + params[1].value + 'm' + '<br>' + params[2].seriesName + ':' + params[2].value + '小时'
          }
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '巡查次数/次',
            minInterval: 1,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            name: '巡查里程/m',
            minInterval: 10,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        dataZoom: [
          {
            type: 'slider',
            show: true
          }
        ],
        series: [
          {
            name: '巡查次数',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(194,53,49)'
              }
            },
            yAxisIndex: 0,
            data: this.rwcs,
            barCategoryGap: '35%'
          },
          {
            name: '巡查里程',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(47,69,84)'
              }
            },
            yAxisIndex: 1,
            data: this.gjlc,
            barCategoryGap: '35%'
          },
          {
            name: '巡查时长',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: 'rgb(97,160,168)'
              }
            },
            yAxisIndex: 0,
            data: this.rwsc,
            barCategoryGap: '35%'
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
