<template>
  <div id="areaManagement-wrapper">
    <QueryBox @searchSrldId="searchSrldId"></QueryBox>
    <div style="height: calc(100% - 65px)">
      <div style="text-align: left;padding-left: 20px;">
        统计维度：
        <RadioGroup type="button" v-model="analysisType" @on-change="handleChangeType">
          <Radio label="sf">行政区划</Radio>
          <Radio label="sa015">所属部门</Radio>
        </RadioGroup>
      </div>
      <div class="showBox">
        <transition name="fade">
          <div class="sta_box sta_box1" :class="{activeBox: boxstate === 1}">
            <div class="con_box">
              <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 :1" size="small" class="boxBtn"></Button>
              <div class="bigBox" :class="{smallToBig:boxstate === 1}">
                <div class="smallBox">
                  <BarChart :data="data1"></BarChart>
                </div>
              </div>
              <Spin size="large" fix v-if="spinShow"></Spin>
            </div>
          </div>
        </transition>
        <div class="sta_box sta_box2" :class="{activeBox: boxstate === 2}">
          <div class="con_box">
            <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 2 ? 0 :2" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 2}">
              <div class="smallBox">
                <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">
                </Table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from './areaQueryBox.vue'
import BarChart from './BarChart.vue'
import {
  // 统计数据
  listPatrol
} from '@/api/areaManagement'
export default {
  name: 'dataAnalysis',
  data () {
    return {
      /* 查询框返回查询条件 */
      obj: '',
      cl002: '', // 任务名称
      comFPkid: '1', // 行政区字段
      uname: '', // 核查人名称
      deptName: '', // 所属部门
      data1: [],
      /* 表格高度 */
      tableHeight: window.innerHeight - 315,
      // loading
      spinShow: false,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '核查人员',
          align: 'center',
          key: 'uname',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        },
        {
          title: '巡查次数(次)',
          align: 'center',
          key: 'rwcs',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        },
        {
          title: '巡查里程(m)',
          align: 'center',
          key: 'gjlc',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        },
        {
          title: '巡查时长(小时)',
          align: 'center',
          key: 'rwsc',
          ellipsis: true,
          tooltip: true,
          minWidth: 80
        }
      ],
      /* 加载 */
      loading: false,
      // 放大缩小状态
      boxstate: 0,
      // 统计维度
      analysisType: 'sf'
    }
  },
  components: {
    QueryBox,
    BarChart
  },
  created () {
    this.getTableData()
  },
  methods: {
    /* 查询框 */
    searchSrldId (obj) {
      this.comFPkid = obj.code
      this.cl002 = obj.cl002
      this.uname = obj.uname
      this.getTableData()
    },
    /* 获取统计数据 */
    /* axios: 获取列表数据 */
    getTableData () {
      this.spinShow = true
      this.loading = true
      listPatrol({
        data: {
          st4ScsCl: {
            cl002: this.cl002
          },
          sysCompany: {
            comFPkid: this.comFPkid
          },
          deptName: this.deptName,
          uname: this.uname,
          groupByName: this.analysisType
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = []
          if (res.data.data.length > 0) {
            // 根据统计维度值显示表格数据
            if (this.analysisType === 'sf') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.sysCompany.comName,
                  rwcs: item.rwcs,
                  gjlc: item.gjlc,
                  rwsc: item.rwsc
                })
              })
            } else if (this.analysisType === 'sa015') {
              res.data.data.forEach((item, index) => {
                this.data1.push({
                  name: item.st4SysSa.sa015,
                  rwcs: item.rwcs,
                  gjlc: item.gjlc,
                  rwsc: item.rwsc
                })
              })
            }
          }
          this.spinShow = false
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    handleChangeType (val) {
      this.spinShow = true
      this.loading = true
      this.getTableData()
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #areaManagement-wrapper{
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .radioBox {
      padding: 0 15px 15px;
      text-align: left;
    }
    .showBox {
      padding: 0 15px;
      height: calc(100% - 32px);
      position: relative;
      .sta_box {
        position: absolute;
        width: 49%;
        overflow: hidden;
        height: 100%;
        padding: 15px;
        background: #fff;
        &.sta_box1 {
          left: 15px;
        }
        &.sta_box2 {
          right: 15px;
        }
        .con_box{
          height: 100%;
          width: 100%;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          .ivu-table-wrapper {
            border-radius: 10px;
          }
          .boxBtn{
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:100%;
            height:100%;
            .smallBox{
              width:100%;
              height:100%;
            }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
          }
        }
      }
    }
    .page_box{
      width: 100%;
      padding: 15px 15px 0;
      text-align: right
    }
    /* box放大 */
    .activeBox{
      width: calc(100% - 15px) !important;
      transition: all 0.4s ease;
      z-index: 999;
    }
    .ivu-radio-group {
      .ivu-radio-wrapper {
        margin-left: 10px;
        border-radius: 4px;
        &:first-child {
          margin-left: 0;
        }
        &.ivu-radio-wrapper-checked {
          background: #2d8cf0;
          color: #fff;
        }
      }
    }
  }
</style>
